import React, { FC, ReactElement } from "react";
import { DocumentsCategories, DocumentSubCategories } from "../../interfaces";
type SelectCategoryProps = {
  sub_categories: DocumentSubCategories[];
  category: DocumentsCategories | null;
  selectCat: Function;
};

const SelectCategory: FC<SelectCategoryProps> = ({
  sub_categories,
  category,
  selectCat,
}): ReactElement => {
  return (
    <div className="p-2">
      <h1 className="text-3xl text-blue-700 mb-1">
        Select a sub-category <b>{category?.title}</b>
      </h1>
      <p className="text-gray-500 ">Please select an item</p>
      <div className="mt-2">
        {sub_categories.map((itm) => (
          <div
            key={itm.user_detail_subcategory_id}
            className="bg-blue-100 rounded p-2 mb-1 hover:bg-blue-200 cursor-pointer"
            onClick={() => selectCat(itm)}
          >
            <h3 className="text-lg font-bold text-blue-800">{itm.title}</h3>
            <p className="m-0 text-sm text-blue-700">{itm.description}</p>
          </div>
        ))}
      </div>
    </div>
  );
};
export default SelectCategory;
