import React, { FC, ReactElement } from "react";
import { MdClose, MdDelete } from "react-icons/md";
import {
  DocumentUser,
  DocumentSubCategories,
  DocumentsCategories,
} from "../../interfaces";
type DocumentDetailsProps = {
  doc: DocumentUser;
  category: DocumentsCategories | null;
  sub_category: DocumentSubCategories | null;
  categories: DocumentsCategories[];
  sub_categories: DocumentSubCategories[];
  close: Function;
  Delete: Function;
  Update: Function;
};

const DocumentDetails: FC<DocumentDetailsProps> = ({
  doc,
  category,
  sub_category,
  close,
  categories,
  sub_categories,
  Delete,
  Update,
}): ReactElement => {
  //  get selected category
  let selectedCat = category;
  let selectedSubCat = sub_category;
  if (category === null) {
    let getCat = categories.find(
      (itm) => itm.user_detail_category_id === doc.category_id
    );
    selectedCat = getCat === undefined ? null : getCat;
  }

  if (
    sub_category === null &&
    doc.sub_category_id !== undefined &&
    doc.sub_category_id.length >= 1
  ) {
    let getSubCat = sub_categories.find(
      (itm) => itm.user_detail_subcategory_id === doc.sub_category_id
    );
    selectedSubCat = getSubCat === undefined ? null : getSubCat;
  }
  return (
    <div className="fixed   top-2 right-0 left-0 bottom-2 z-50  w-full  max-w-screen-md  max-w-screen-lg- h-auto m-auto">
      <div className="max-h-screen bg-white  overflow-auto pb-4">
        <div className="p-2 border-b flex items-center">
          <div className="flex-1">
            <h4 className="text-xl m-0 text-bold text-gray-700">
              {selectedCat?.title}{" "}
              {selectedSubCat !== null && " - " + selectedSubCat.title}
            </h4>
          </div>
          <div className="">
            <button
              className="p-1 rounded-full  bg-red-400 text-white"
              onClick={() => close()}
            >
              <MdClose />
            </button>
          </div>
        </div>

        {doc.files !== undefined && doc.files.length > 1 && (
          <div className="bg-gray-900 w-full p-1">
            <img
              src={doc.files}
              alt="Document file details"
              className="w-full rounded"
            />
          </div>
        )}
        <div className="p-3 ">
          <div className="flex">
            <div className="flex-1">
              {doc.approved !== undefined && (
                <span className="text-sm bg-yellow-200 py-0.5 px-2 rounded-full text-yellow-800">
                  {doc?.approved}
                </span>
              )}
              <h2 className="text-xl mt-1 mb-4 font-bold text-blue-800">
                {doc.title}
              </h2>
            </div>

            {/* <div className="ml-1">
              <button
                onClick={() => Update(doc.doc_id)}
                className="py-1 rounded px-3 text-sm mb-1 block w-full bg-blue-200 hover:bg-blue-300 text-blue-800"
              >
                <MdModeEdit className="inline-block" /> Edit
              </button>
            </div> */}
            <div className="ml-1">
              <button
                onClick={() => {
                  Delete(doc.doc_id);
                  close();
                }}
                className="py-1 rounded px-3 text-sm mb-1 block w-full bg-red-200 hover:bg-red-300 text-red-800"
              >
                <MdDelete className="inline-block" /> Delete
              </button>
            </div>
          </div>

          <div className="">
            {doc.description.length >= 1 && (
              <div className="mb-2 py-1 border-b pb-3 border-gray-200">
                <label className="block text-gray-500 text-sm">
                  Description:
                </label>
                <span className="">{doc.description}</span>
              </div>
            )}

            <div className="grid grid-cols-2 gap-4">
              {selectedCat !== null && (
                <div className="mb-2 py-1 border-b border-gray-200">
                  <label className="block text-gray-500 text-sm">
                    Category:
                  </label>
                  <b>{selectedCat.title}</b>
                </div>
              )}
              {selectedSubCat !== null && (
                <div className="mb-2 py-1 border-b border-gray-200">
                  <label className="block text-gray-500 text-sm">
                    Sub category:
                  </label>
                  <b>{selectedSubCat.title}</b>
                </div>
              )}
              {doc.school !== undefined &&
                doc.school.title !== undefined &&
                doc.school.title.length >= 1 && (
                  <div className="mb-2 py-1 border-b border-gray-200">
                    <label className="block text-gray-500 text-sm">
                      School:
                    </label>
                    <b>{doc.school.title}</b>
                  </div>
                )}
              {doc.company !== undefined && doc.company.length >= 1 && (
                <div className="mb-2 py-1 border-b border-gray-200">
                  <label className="block text-gray-500 text-sm">
                    Company:
                  </label>
                  <b>{doc.company}</b>
                </div>
              )}
              {doc.names !== undefined && doc.names.length >= 1 && (
                <div className="mb-2 py-1 border-b border-gray-200">
                  <label className="block text-gray-500 text-sm">Names:</label>
                  <b>{doc.names}</b>
                </div>
              )}
              {doc.email !== undefined && doc.email.length >= 1 && (
                <div className="mb-2 py-1 border-b border-gray-200">
                  <label className="block text-gray-500 text-sm">Email:</label>
                  <b>{doc.email}</b>
                </div>
              )}
              {doc.phone_number !== undefined && doc.phone_number.length >= 1 && (
                <div className="mb-2 py-1 border-b border-gray-200">
                  <label className="block text-gray-500 text-sm">
                    Phone Number:
                  </label>
                  <b>{doc.phone_number}</b>
                </div>
              )}
              {doc.start_year !== undefined && doc.start_year.length >= 1 && (
                <div className="mb-2 py-1 border-b border-gray-200">
                  <label className="block text-gray-500 text-sm">
                    Start Year:
                  </label>
                  <b>{doc.start_year}</b>
                </div>
              )}
              {doc.end_year !== undefined && doc.end_year.length >= 1 && (
                <div className="mb-2 py-1 border-b border-gray-200">
                  <label className="block text-gray-500 text-sm">
                    End Year:
                  </label>
                  <b>{doc.end_year}</b>
                </div>
              )}
              {doc.year !== undefined && doc.year.length >= 1 && (
                <div className="mb-2 py-1 border-b border-gray-200">
                  <label className="block text-gray-500 text-sm">Year:</label>
                  <b>{doc.year}</b>
                </div>
              )}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};
export default DocumentDetails;
