import React, { FC, Fragment, ReactElement } from "react";
import { MdCardMembership, MdInsertDriveFile } from "react-icons/md";
import {
  DocumentUser,
  DocumentSubCategories,
  DocumentsCategories,
} from "../../interfaces";
type DocumentsListProps = {
  documents: DocumentUser[];
  selected_category: DocumentsCategories | null;
  selected_sub_category: DocumentSubCategories | null;
  selectDoc: Function;
  addNewDoc: Function;
  categories: DocumentsCategories[];
  sub_categories: DocumentSubCategories[];
};

const DocumentsList: FC<DocumentsListProps> = ({
  documents,
  selected_category,
  selected_sub_category,
  selectDoc,
  addNewDoc,
  categories,
  sub_categories,
}): ReactElement => {
  let documents_list: DocumentUser[] = [];
  if (selected_category === null) {
    // * no category selected
    documents_list = documents;
  } else {
    //* nothing is selected in subcategory (default)
    if (selected_sub_category === null) {
      // check if the category has sub categories
      documents_list = documents.filter(
        (doc: DocumentUser) =>
          doc.category_id === selected_category?.user_detail_category_id
      );
    } else if (selected_sub_category !== null) {
      documents_list = documents.filter(
        (doc: DocumentUser) =>
          doc.category_id === selected_category.user_detail_category_id &&
          doc.sub_category_id ===
            selected_sub_category.user_detail_subcategory_id
      );
    }
  }
  return (
    <div className="col-span-4  shadow rounded bg-white bg-opacity-50">
      <div className="p-3 border-b border-gray-200 flex bg-white justify-between">
        <h1 className="text-3xl m-0 font-bold text-gray-600">
          {selected_category === null ? (
            "All details"
          ) : (
            <Fragment>
              {selected_category?.title}{" "}
              {selected_sub_category !== null &&
                " - " + selected_sub_category.title}
            </Fragment>
          )}
        </h1>
        <button
          className="px-3 py-1 bg-blue-600 rounded hover:bg-blue-800 text-white"
          onClick={() => addNewDoc()}
        >
          Add new
        </button>
      </div>
      <div className="p-2 ">
        {documents_list.length <= 0 ? (
          <div className="p-4 text-center  text-gray-500">
            <MdCardMembership className="text-7xl mx-auto my-2" />
            <h2 className="text-3xl">No data found on</h2>
            <h1 className="text-xl font-bold text-blue-900">
              {selected_category === null
                ? "Any category"
                : selected_category?.title}{" "}
              {selected_sub_category !== null &&
                " - " + selected_sub_category?.title}
            </h1>
            <p>
              Please click on <b>Add new</b> and improve your CV
            </p>
          </div>
        ) : (
          documents_list.map((doc: DocumentUser, i) => {
            let category_itm = categories.find(
              (itm) => itm.user_detail_category_id === doc.category_id
            );

            let sub_category_itm: any = null;
            if (doc.sub_category_id.length >= 1) {
              sub_category_itm = sub_categories.find(
                (itm) => itm.user_detail_subcategory_id === doc.sub_category_id
              );
            }
            return (
              <div
                key={i}
                className="flex p-1 mb-2 rounded hover:shadow border hover:bg-blue-100  border-blue-100  items-center bg-white"
                onClick={() => selectDoc(doc)}
              >
                <div className="flex-1 pl-2">
                  <h3 className="text-xl font-bold text-blue-800">
                    {doc.title}
                  </h3>
                  {category_itm !== undefined && (
                    <p className="text-sm m-0">
                      <i className="text-gray-400">Category: </i>{" "}
                      <b>
                        {category_itm.title}
                        {selected_sub_category !== null &&
                          sub_category_itm !== null &&
                          sub_category_itm !== undefined &&
                          " - " + sub_category_itm?.title}
                      </b>
                    </p>
                  )}
                </div>
                <div className=" mx-2">
                  <button className="p-2">
                    <MdInsertDriveFile className="text-5xl text-blue-800" />
                  </button>
                </div>
                <div className="pr-1">
                  {/* <button className="p-1 w-full text-sm bg-red-300 text-red-700 rounded mb-1 block">
                  Remove
                </button>
                <button className="p-1 text-sm w-full bg-blue-300 text-blue-800 rounded mb-1 block">
                  Update
                </button> */}
                  {/* <button className="p-1 text-xs bg-blue-300 rounded mb-1 block">
              Edit button
            </button> */}
                </div>
              </div>
            );
          })
        )}
      </div>
    </div>
  );
};
export default DocumentsList;
