import React, { FC, ReactElement, Fragment } from "react";
import {
  DocumentSubCategories,
  DocumentsCategoriesWithCount,
  DocumentsCategories,
} from "../../interfaces";
type VideoCardSupervisorProps = {
  categories: DocumentsCategoriesWithCount[];
  sub_categories: DocumentSubCategories[];
  selected_category: DocumentsCategories | null;
  selected_sub_category: DocumentSubCategories | null;
  SelectCategoty: Function;
  SelectSubCategory: Function;
  total_documents: number;
};

const VideoCardSupervisor: FC<VideoCardSupervisorProps> = ({
  categories,
  sub_categories,
  selected_category,
  selected_sub_category,
  SelectCategoty,
  SelectSubCategory,
  total_documents,
}): ReactElement => {
  let all_sub_categories: DocumentSubCategories[] = [];

  if (selected_category !== null) {
    all_sub_categories = sub_categories.filter(
      (sub_c) => sub_c.category_id === selected_category.user_detail_category_id
    );
  }

  return (
    <div className=" bg-white- shadow- rounded p-2-">
      <h2 className="text-xl font-bold text-gray-500 mb-2">Categories</h2>

      <div className="mt-1">
        <button
          onClick={() => SelectCategoty(null)}
          className={`p-2 rounded  w-full text-left  flex items-center ${
            selected_category === null
              ? "bg-blue-700 text-white"
              : "bg-blue-200 text-blue-800 hover:bg-blue-600 hover:text-white"
          } mb-1  font-bold`}
        >
          <span className="flex-1">All</span>
          {total_documents >= 1 && (
            <b className="border border-white text-white bg-blue-600 py-0.5 -my-0.5 px-2 rounded-full text-sm">
              {total_documents}
            </b>
          )}
        </button>
        {categories.map((cat, i) => (
          <Fragment key={i}>
            <button
              onClick={() => SelectCategoty(cat)}
              className={`p-2 rounded  w-full text-left flex items-center ${
                selected_category?.user_detail_category_id ===
                cat.user_detail_category_id
                  ? "bg-blue-700 text-white"
                  : "bg-blue-200 text-blue-800 hover:bg-blue-600 hover:text-white"
              } mb-1  font-bold`}
            >
              <span className="flex-1">{cat.title}</span>
              {cat.count >= 1 && (
                <b className="border border-white text-white bg-blue-600 py-0.5 -my-0.5 px-2 rounded-full text-sm">
                  {cat.count}
                </b>
              )}
            </button>
            {selected_category?.user_detail_category_id ===
              cat.user_detail_category_id &&
              all_sub_categories.length >= 1 && (
                <div className="ml-4 mb-3">
                  <button
                    onClick={() => SelectSubCategory(null)}
                    className={` ${
                      selected_sub_category === null
                        ? "bg-blue-500 mb-1  text-white"
                        : "bg-blue-100 mb-1 hover:bg-blue-300 text-blue-800"
                    } py-1 px-2 rounded block w-full text-left  font-bold`}
                  >
                    All
                  </button>
                  {all_sub_categories.map((scat, x) => (
                    <button
                      key={x}
                      onClick={() => SelectSubCategory(scat)}
                      className={` ${
                        selected_sub_category?.user_detail_subcategory_id ===
                        scat.user_detail_subcategory_id
                          ? "bg-blue-500 mb-1  text-white"
                          : "bg-blue-100 mb-1 hover:bg-blue-300 text-blue-800"
                      } py-1 px-2 rounded block w-full text-left  font-bold`}
                    >
                      {scat.title}
                    </button>
                  ))}
                </div>
              )}
          </Fragment>
        ))}
      </div>
    </div>
  );
};
export default VideoCardSupervisor;
